.imageuploadify1{
	padding-top:20px;
	overflow: hidden;
	position:relative;
	min-height:200px;
	min-width:250px;
	margin:auto;
	display:flex;
	flex-direction:column;
	text-align:center;
	color:#3AA0FF;
	.imageuploadify-overlay{
		z-index:10;
		width:100%;
		height:100%;
		position:absolute;
		flex-direction:column;
		top:0;
		left:0;
		display:none;
		font-size:7em;
		background-color:rgba(242,242,242,.7);
		text-align:center;
		pointer-events:none;
	}
	.imageuploadify-overlay i{
		z-index:10;
		position:absolute;
		top:50%;
		left:50%;
		transform:translate(-50%,-50%);
		pointer-events:none;}
	.imageuploadify-images-list{
		background: $lightGray2;
		border: 2px dashed #d2d2d2;
		position: relative;
		border-radius: 10px;
		display:inline-block;
		min-height: 200px;
    	min-width: 250px;
	}.imageuploadify-images-list i{
		display:block;
		font-size:1em;
		text-align:center;
		padding:0;}
	.imageuploadify-images-list span.imageuploadify-message{
		font-size:24px;
		border-top:1px solid #3AA0FF;
		border-bottom:1px solid #3AA0FF;
		padding:10px;
		display:inline-block;
	}
	.imageuploadify-images-list button.btn-default{
		padding:0;
		width:35px;
		height:35px;
		text-align:center;
		background:$lightGray2;
		display:block;
		color:#ff932a;
		border-color:#ff932a;
		border-radius:50%;
		margin:100px auto;
	}
	.imageuploadify-images-list .imageuploadify-container{
		background:$lightGray2;
		position:relative;
		margin-bottom:1em;
		float:left;
		border-radius:12px;
	}
	.imageuploadify-images-list .imageuploadify-container img{
		border-radius:10px;min-height:auto;min-width:100%;}
	.imageuploadify-images-list .imageuploadify-container .imageuploadify-details{
		position:absolute;
		top:0;
		padding-top:20px;
		width:100%;
		height:100%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		background:rgba(255,255,255,.5);
		z-index:2;opacity:0;}
	.imageuploadify-images-list .imageuploadify-container .imageuploadify-details span{
	 	display:block}
	.imageuploadify-view{
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		background:transparent;
		cursor: pointer;
		text-align: center;
		width: 100%;
	    height: 100%;
	    z-index: 3;
	    transition: all 1s;
		i{
			color: #fff;
			margin-top: 100px;
			font-size: 3em;
		}
	}
	.imageuploadify-images-list .imageuploadify-container{
	    position: absolute;
	    top: 0;
		width: 100%;
	    height: 100%;
	}
	.btnRefresh {
		position: absolute;
	    top: -15px;
	    left: 20px;
	    width: 25px;
	    height: 25px;
	    border-radius: 50%;
	    font-size: 10px;
	    line-height: 1.42;
	    padding: 2px 0;
	    text-align: center;
	    z-index: 3;
	    background: #2aacff;
	    color: #fff;
	}
	.btnDelete{
		position: absolute;
	    top: -15px;
	    left: 50px;
	   width: 25px;
	    height: 25px;
	    border-radius: 50%;
	    font-size: 10px;
	    line-height: 1.42;
	    padding: 2px 0;
	    text-align: center;
	    z-index: 3;
	    background:#cb4d5c;
	    color: #fff;
	}
	.imgViewWrap{
		width: 100%;
	    height: 100%;
	    overflow: hidden;
	    /* padding: 5px; */
	    border-radius: 10px;
	}
}
.imageuploadify2{
	position:relative;
	margin:auto;
	display:flex;
	padding:0;
	flex-direction:column;
	text-align:center;
	background-color:$lightGray2;
	color:#3AA0FF;
	padding: 60px 15px;
	.btnRefresh {
		position: absolute;
	    top: -15px;
	    left:-10px;
	    width: 25px;
	    height: 25px;
	    border-radius: 50%;
	    font-size: 10px;
	    line-height: 1.42;
	    padding: 2px 0;
	    text-align: center;
	    z-index: 3;
	    background: #2aacff;
	    color: #fff;
	}
	.btnDelete{
		position: absolute;
	    top: -15px;
	    right: -10px;
	    width: 25px;
	    height: 25px;
	    border-radius: 50%;
	    font-size: 10px;
	    line-height: 1.42;
	    padding: 2px 0;
	    text-align: center;
	    z-index: 3;
	    background:#cb4d5c;
	    color: #fff;
	}
	.imageuploadify-overlay{z-index:10;width:100%;height:100%;position:absolute;flex-direction:column;top:0;left:0;display:none;font-size:7em;background-color:rgba(242,242,242,.7);text-align:center;pointer-events:none}
	.imageuploadify-overlay i{z-index:10;position:absolute;top:50%;left:50%;transform:translate(-50%,-50%);pointer-events:none}
	.imageuploadify-images-list{
		display:inline-block;
	}
	.imageuploadify-images-list i{display:block;text-align:center;margin-top:.5em;padding-bottom:12px}
	.imageuploadify-images-list span.imageuploadify-message{font-size:24px;border-top:1px solid #3AA0FF;border-bottom:1px solid #3AA0FF;padding:10px;display:inline-block}
	.imageuploadify-images-list button.btn-default{
		background:#fff;
		border:0;
		display:inline-block;
		float:right;
		color:$orange;
		border-radius:1em;
		width:100px;
		height: 100px;
		i{
			border: 1px solid;
		    display: inline-block;
		    font-size: 1em;
		    text-align: center;
		    padding: 0;
		    width: 35px;
		    height: 35px;
		    line-height: 35px;
		    border-radius: 50%;
		}
	}
	.imageuploadify-images-list .imageuploadify-container{width:100px;height:100px;position:relative;margin-bottom:1em;float:right;margin-left:30px;border-radius:12px;box-shadow:0 0 4px 0 #888;}
	.imageuploadify-images-list .imageuploadify-container button.btn-danger{position:absolute;top:3px;right:3px;width:25px;height:25px;border-radius:50%;font-size:10px;line-height:1.42;padding:2px 0;text-align:center;z-index:3;}
	.imageuploadify-images-list .imageuploadify-container img{height:100px;left:50%;position:absolute;top:50%;transform:translate(-50%,-50%);width:auto;}
	.imageuploadify-images-list .imageuploadify-container .imageuploadify-details{position:absolute;top:0;padding-top:20px;width:100%;height:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;background:rgba(255,255,255,.5);z-index:2;opacity:0}.imageuploadify-images-list .imageuploadify-container .imageuploadify-details span{display:block;}
}
.imageuploadify-images-list:hover .imageuploadify-view{
	opacity: 1;
}
