.payBanner{
	background-image: url('../imgs/paymentBanner.png');
}
.payment{
	background:$lightGray;
    width: 100%;
    font-family: logoFont;
    padding-bottom: 60px;
	.banks{
		padding:30px 0;
	}
	.form h3{
		margin-bottom: 30px;
	}
	.bank{
		background:$lightGray2;
		border-radius: 10px;
		padding:15px 0;
		box-shadow:1px 2px 8px 3px $shadowGray;
		margin:30px 0 60px;
		.bankImg{
			height: 40px;
			margin-bottom: 30px;
			img{
				max-height: 100%;
			}
		}
		h4{
			color: $medGray;
		}
		h5{
			color: $gray;
		}
	}
	.imageuploadify{
		width: 100%;
		display: block;
		clear: both;
		padding:0;
	}
	.imageuploadify1{
		overflow:visible;
	}
	.imageuploadify-images-list.text-center{
		min-width: 100%;
		min-height: 350px;
		background: #fff;
	}
	.imageuploadify1 .imageuploadify-images-list button.btn-default{
		background:#fff;
		margin: 150px auto;
	}
	.imageuploadify1 .imageuploadify-view i {
	    color: #fff;
	    margin-top: 150px;
	}
}
.lightGrayForm{
	background: $lightGray2;
	float: right;
    padding: 30px 0;
    border-radius: 10px;
    font-family: plain;
	label{
		color: $gray;
		float: right;
		font-weight: 400;
		margin-bottom: 10px;
	}
	select{
		width: 100%;
		background:#fff;
		border:0;
    	padding: 3px 12px;
    	border-radius: 4px;
    	height: 34px;
    	margin-bottom: 15px;
	}
	input{
		border:0;
		border-radius: 4px;
		height: 34px;
		margin-bottom: 15px;
	}
	button{
		width: 100%;
	    padding: 12px;
	    border-radius: 4px;
	    margin-top: 30px;
	    font-weight: 400;
	}
}
.datepicker{
	direction: rtl;
	right: auto;
	border:0;
}
.datepicker-dropdown.datepicker-orient-top:before{
	border-top:0;
}
