.login,.register{
	/*background:url('../imgs/backG.jpg') no-repeat;*/
	background: #fff;
	background-size: 100% 100%;
	.loginCont{
		background:#fff;
		margin: 60px auto;
	}
	.registerCont{
		margin: 60px auto;
		select{
			padding-top: 0;
		}
	}
	h3{
		color: $gray;
    	margin-bottom: 30px;
		span{
			margin:0 2px;
		}
	}
	.alert{
		position: relative;
	    top: 50px;
	    left: 0;
	    right: 0;
	    text-align: center;
	}
	select{
	    display: block;
	    clear: both;
	    width: 100%;
	    border: 1px solid #ccc;
	    border-radius: 0;
	    background: transparent;
	    margin-top: 15px;
	    padding: 6px 10px;
	    height: 34px;
	    color: $gray;
	}
	.dropdown-menu{
		width: 100%;
    	border-radius: 0;
	}
	.alert{
		display: none;
		.close{color: #fff;opacity: 1;text-shadow: none;}
	}
	label.error{
		margin-top: -20px;
    	margin-bottom: 10px;
	}
}
.form-Input{
	label{
		color: $blue;
		font-size:1em;
		margin-bottom: 0;
		font-weight: 400;
	}
}
input.form-control{
	box-shadow: none;
	border-radius: 0;
	margin-top: 15px;
	margin-bottom:30px;
}
.remember{
	a{
		float: left;
		color: $gray;
		cursor: pointer;
		font-weight: bold;
	}
	label{
		margin-right: 5px;
    	span{
    		margin-right: 5px;
    	}
	}
}
.submit{
	text-align: center;
	.btn{
		margin-top: 30px;
		padding: 16px 48px;
	}
}
.hasAcc{
	text-align: center;

}
.h3 span{
	color: $blue;
}
.logoWrap{
	text-align: center;
	margin-bottom: 45px;
	img{
		display: block;
    	margin: auto;
	}
	.En{
		line-height: .7em;
	    font-family: en;
	    color: $blue;
	    display: block;
	    font-weight: 600;
	    font-size: 1.4em;
	}
	.Ar{
		clear: both;
	    display: block;
	    font-family: logoFont;
	    font-size: 3.5em;
	    line-height: .5em;
	    margin-left: 50px;
	    color: $gray;
	}
}
/*     forget password     */
.login .carousel,.register .carousel{
	button.carousel-control{
	    position: relative;
	    opacity: 1;
	    width: auto;
	    text-shadow: none;
	    padding: 16px 90px;
	    margin-top: 60px;
	    &:hover,&:focus{opacity:.8;}
	}
	.form-input{
		margin: 30px 0;
	}
	.lastC{
	    padding: 16px 80px;
	    margin-bottom: 60px;
	}
    .carousel-indicators{ 
    	bottom: 150px;
    	li{
	    	background:$orange;
	    	border:0;
	    }
	    li.active ~ li{
	    	background: $lightGray;
	    }
	    .active{
	    	width: 10px;
	    	height: 10px;
	    	margin: 1px;
	    }
	}
}
.cancel{
	text-align: center;
	margin-top: 30px;
	a{color: $gray;cursor: pointer;}
}
.forgetPass{
	display: none;
}
/*     confirm     */
.credit {
    display: none;
}
.card{
    min-height: 600px;
    padding:100px;
}
.credit-cell{
    width: calc((100% / 4) - 10px);
    height: 45px;
    padding: 6px 12px;
    background-color: #fff;
    border:1px solid $lightGray;
    text-align: center;
    margin-left:10px;
    float: left;
}
.creditWrap{
	margin:0 60px;
}
.credit-input {
    display: block;
    width:100%;
    font-size: 14px;
    color: #555;
    text-align: center;
    float: right;
    margin: 30px 0;
    -webkit-transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
}