footer{
	position: absolute;
	bottom: 0;
	min-height: 52px;
	color: #fff;
	text-align: center;
	padding: 15px;
	width: 100%;
	background:$black;
	clear: both;
	p{margin: 0;font-size: 1.2em;span{margin: 0 5px;}}
}

@keyframes bounce {
  0%, 20%, 53%, 80%, 100% {
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    transform: translate3d(0,0,0);
  }

  40% {
    transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
    transform: translate3d(0, -6px, 0);
  }

  70% {
    transition-timing-function: cubic-bezier(0.755, 0.050, 0.855, 0.060);
    transform: translate3d(0, -4px, 0);
  }

  90% {
    transform: translate3d(0,-2px,0);
  }
}
.socialLinks li{
	a{
		position: relative;
	}
	&:hover,&:focus {
	    animation: bounce 1s;
	}
}
.footerPlus{
	background:$darkBlue;
	padding: 60px 0;
	.logoWrap3{
		margin-bottom: 15px;
	}
	h4{
		margin-bottom: 15px;
	}
	p,a{
		color: #fff;
	}
	ul{
		padding:0;
	}
	li{
		list-style: none;
	}
}
.logoWrap3{
	.En {
	    font-size: 1em;
	    line-height: .5em;
	    float: left;
	    color: #fff;
	}
	.Ar{
		font-size: 1.8em;
	    color: #fff;
	    line-height: 1em;
	}
}
.socialLinks{
	text-align: center;
	margin-top: 30px;
	li{
		display: inline-block;
		background:$blue;
		min-width: 30px;
	    min-height: 30px;
	    text-align: center;
	    border-radius: 50%;
	    margin:0 5px;
	    i{
	    	line-height: 30px;
	    }
	}
}