.homeBanner{
	padding: 200px 0 140px;
    text-align: center;
    background-color:$darkBlue;
    background-image: url(../imgs/bannerHome.png);
    background-repeat: no-repeat;
    background-size:cover;
	.lookFor{
		color: #fff;
	    padding: 60px;
	    background: rgba(255,255,255,.1);
	    border-radius: 10px;
	    .orangeBtn{
	    	margin-top: 60px;
    		padding: 10px 60px;
	    }
	    select,input{
	    	display: block;
		    width: 100%;
		    color:$medGray;
		    border: 0;
		    padding:0 5px;
		    height: 30px;
		    background-color:#fff;
	    }
	}
	.itemDrop{
	    label{
		    float:right;
		    margin-bottom:10px;
	    }
	}
	
	input[type=number]::-webkit-inner-spin-button { 
	    -webkit-appearance: none;
	    cursor:pointer;
	    display:block;
	    width:8px;
	    color: #333;
	    text-align:center;
	    position:relative;
	}

	input[type=number]:hover::-webkit-inner-spin-button { 
	    background: url('../imgs/up-and-down-small-triangular-arrows.png') no-repeat 0% 0%; 
	    background-size: 100% 100%; 
	    width: 14px;
	    height: 14px;
	    padding: 4px;
	    position: relative;
	    right: 4px;
	    top: 2px;
	}
	.dropdown{
		position: relative;
	    width: 100%;
	    .btn{
	    	width: 100%;
			background: #fff;
			box-shadow: none;
			text-align: right;
			color: $medGray;
			border-radius: 0;
			.caret{
				float:left;
				color:$blue;
				margin-top:10px;
			}
	    }
	    ul{
	        width: 100%;
    		border-radius: 0;
    		li a{
    			color:$medGray;
    		}
	    }
	}
}
.headDiv{
	text-align: center;
	h2{
		margin-bottom:15px;
	}
	h4{
		margin-bottom:30px;
	}
}
/*     latest      */

.oneHome p{
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}
.latest{
	padding: 60px 0;
	background:$lightGray;
	.nav-pills>li>a {
	    padding: 10px 30px;
	    border-radius: 0;
	}
	.item{
		text-align: right;
	}
	.nav-pills{
		text-align: center;
		margin-bottom:30px;
		li{
			display: inline-block;
    		float: none;
    		margin-left: 5px;
		}
		li>a{
			background:#fff;
			color: $blue;
			min-width: 160px;
		}
		li.active>a,li.active>a:focus,li.active>a:hover {
		    background-color: $blue;
		    color: #fff;
		}
	}
	.oneHome{
		box-shadow: 0px 4px 10px 1px #ccc;
		p,i,span:not(.blue){
			color: $medGray;
		}
	}
	.since{
		margin-right: -10px;
		margin-left: -10px;
	    padding-right: 10px;
	    padding-left: 10px;
	    background: $lightGray;
	    color: $gray;
	    width: calc(100% + 20px);
	}
	.carousel-inner{
		z-index: 2;
	}
}
.latest .carousel-control{
	opacity: 1;
	&.left{
		right:-50px;
		i{
			right:0px;
		}
	}
	&.right{
		left:-50px;
		i{
			left:0px;
		}
	}
	i{
		color: $orange;
		text-shadow: none;
	    background: #fff;
	    padding: 5px 10px;
	    border-radius: 4px;
		transition: all 1s;
	}
	&:hover i,&:focus i{
		background:$orange;
		color: #fff;
	}
}
.oneHome:hover,.oneHome:focus{
	opacity: .9;
	transition: all 1s;
}
.homeImg{
		position: relative;
		height: 210px;
    	width: 100%;
    	overflow:hidden;
		i.fa-heart{
			position: absolute;
			top: 10px;
			right: 10px;
			color: rgba(255,255,255,.5);
			-webkit-text-stroke-width: 1px;
		    -webkit-text-stroke-color: #fff;
		    font-size: 1.5em;
		    cursor: pointer;
		    transition: all 1s;
		}
		i.fa-heart.clicked{
			color:$blue;
			-webkit-text-stroke-color: $blue;
		}
}