.profileCont{
	.tab-content{
		h4{
			margin-bottom: 30px;
		}
	}
}
.editOverlay{
	position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, .6);
    z-index: 9;
    cursor: pointer;
    text-align: center;
    padding-top: 50%;
    opacity: 0;
    transition: all 2s;
    a{
    	font-size:3em;
    }
}
.profileCont .oneHome:hover,.profileCont .oneHome:focus{
	.editOverlay{
		opacity: 1;
	}
}
.proDet{
	margin-top: -45px;
	position: relative;
	float: right;
    width: 100%;
	.imgWrap{
		position: relative;
		width:100%;
		height: 170px;
		overflow:hidden;
		background:#001f35;
		img{
			width: 100%;
			min-height:100%;
		}
		a{
			position: absolute;
			left: 0;
			bottom: 0;
			background:$orange;
			color: #fff;
			padding: 6px;
		}
	}
	.proName{
		position: absolute;
		bottom: 0;
		margin-right:16.66666667%;
		h4{
			font-family: logoFont;
			font-size: 2em;
			margin-bottom: 15px;
		}
		h5{
			font-size: 1.2em;
    		line-height: 1.5em;
    		margin-bottom: 15px;
    		i{
    			margin-left: 5px;
    		}
		}
		h5:last-child{
			margin-bottom: 0;
		}
	}
}
/*      side menu       */
.sideMenu{
    background: $lightGray;
    margin-top: 30px;
    padding: 90px;
    .imageuploadify1,.imageuploadify1 .imageuploadify-images-list{
    	min-width:100%;
    }
    .orangeBtn{
    	margin-top: 30px;
    	padding:6px 25px;
    }
    .nav-justified>li{
    	display: inline-block;
    	width: 100%;
    	a{
    		color: gray;
    		text-align: right;
    		opacity: .39;
    		font-family: logoFont;
    		padding: 25px 15px;
    		i{
    			float: right;
    			margin-left: 10px;
    		}
    	}
    }
    .nav-pills>li.active>a, .nav-pills>li.active>a:focus, .nav-pills>li.active>a:hover{
    	background:#fff;
    	opacity: 1;
    	color: $gray;
    	i{
    		color: $blue;
    	}
    }
}
.grayForm{
    label{
    	color: $gray;
    }
    .form-input{
    	.form-control{
    		border:0;
    		margin-bottom:15px;
    		border-radius: 4px;
    	}
    }
    .tab-pane{
    	h4{
    		color: $blue;
    		font-size: 1.8em;
		    margin-bottom: 30px;
		    margin-top: 0;
    	}
    	.orangeBtn{
    		margin-top: 15px;
    	}
    }
}
.oneHome{
	position: relative;
	color: $medGray;
	float: right;
    width: 100%;
    margin-bottom: 30px;
	.delete{
		position: absolute;
		left: 10px;
		top: 0;
		background:$red;
		color: #fff;
		padding: 15px;
		cursor: pointer;
		font-size: 1.3em;
		z-index: 2;
	}
	.edit{
		position: absolute;
		left: 70px;
		top: 0;
		background:$blue;
		color: #fff;
		padding:15px 13px;;
		cursor: pointer;
		font-size: 1.3em;
		z-index: 2;
	}
	.since{
		line-height: 1.5;
		margin:0;
	}
	.homeDet{
		background:#fff;
		float: right;
		width: 100%;
		padding:0 10px;
		padding-bottom: 10px;
		>div:not(:last-child){
			border-bottom: 1px solid $lightGray;
		}
		>div:last-child{
			span{font-size:12px;}
		}
		>div{
			padding: 10px 0;
		}
		h3{
			margin-top: 0;
			color: $gray;
		}
		.num{
			font-size: 1.5em;
		}
	}
	i {
	    margin-left: 2px;
	}
	.edit i{
		color: #fff;
	}
}
.bar ul{padding: 0;}
.homeImg{
	img{
		width: 100%;
		min-height: 100%;
		overflow: hidden;
	}
}
.left{
	text-align: left;
}
.blue{
	color: $blue;
}
.modal-content{
	border-radius: 0;
	text-align: center;
	.h5 a{color: $gray;}
	.btn{
		padding: 6px 50px;
    	margin-bottom: 15px;
	}
}
.fileInputWrap{
	position: relative;
	input{
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		opacity: 0;
	}
}
.addInput{
	position: relative;
	i{
		position: absolute;
	    left: -30px;
	    top:10px;
	    cursor: pointer;
	}
}
.relative{position: relative;}
.scrolledDiv{
	max-height:550px;
	.subscribeDiv{
		margin-bottom:60px;
		float: right;
		width: 100%;
	}
}