.viewprofile {
	.proDet{
		margin-bottom: 30px;
	}
}
.viewProfileBanner{
	background-image: url('../imgs/viewprofileBanner.png');
}
.profileSearch{
	background:$lightGray;
	padding:30px 0;
	clear: both;
	.who{
		border-bottom: 1px solid #fff;
		padding-bottom: 30px;
		margin-bottom: 30px;
		h4{
			font-family: logoFont;
		}
		p{
			color: $medGray;
		}
	}
	.oneHome .since {
	    margin-right: -10px;
	    margin-left: -10px;
	    padding-right: 10px;
	    padding-left: 10px;
	    background: #ebebeb;
	    margin-top: -10px;
	}
	.oneHome {
	    box-shadow: 0px 4px 10px 1px #ccc;
	}
}