.searchRes{
	background: $lightGray;
	.oneHome{
		box-shadow: 0px 4px 10px 1px #ccc;
		p,span:not(.blue){
			color: $medGray;
		}
		.since{
			margin-right: -10px;
		    margin-left: -10px;
		    padding-right: 10px;
		    padding-left: 10px;
		    background: $lightGray;
		    margin-top: -10px;
		}
	}
}
.blueForm {
	form{
		input.form-control{
			width: 90%;
		}
		.input-group-addon{
			padding-bottom: 4px;
    		border-color: rgba(255,255,255,.5);
		}
	}
}
.blueSearch form input.form-control {
    width: 100%;
}
.openSearch{
	display: block;
    margin-bottom: 10px;
    background: #fff;
    padding: 10px;
    border-radius: 50%;
    color: #949b9f;
    box-shadow: 0px 4px 10px 1px #ccc;
    cursor: pointer;
}
.searchBanner{
	background-image:url('../imgs/headBack.jpg');
}
.blueSearch{
		padding: 15px;
	    float: right;
	    width: 100%;
	    margin-top: -30px;
	    font-family: logoFont;
	    h3{margin-top: 10px;}
	    select{
	    	background-color: transparent;
		    width: 100%;
		    border: 1px solid rgba(255,255,255,.5);
		    height: 34px;
		    margin-top: 3px;
		    padding:5px;
		    background-image: url(../imgs/selectArrowWhite.png);
    		background-repeat: no-repeat;
		    option{
		    	color: $gray;
		    }
	    }
	    .dropdown button{
	    	margin-top:5px;
	    	border-width:1px;
	    	border-color: rgba(255,255,255,.5);
	    	width: 100%;
	    	text-align: right;
	    	padding: 8px 20px;
	    	span{float: left;    margin-top: 10px;}
	    }
}
.blueSearch .mapIcon{
		float: left;
	    background-color: #26a6f8;
	    border-radius: 50%;
	    width: 35px;
	    height: 35px;
	    text-align: center;
	    padding:0;
	    border:0;
	    margin-top: 5px;
	    i{
	    	line-height: 35px;
	    }
}
.blueSearch input[type=number]{
	border:1px solid rgba(255,255,255,0.5);
	height: 34px;
	margin: 3px 0 0;
	padding: 5px;
}
.searchDet{
	input[type=number]::-webkit-inner-spin-button { 
	    -webkit-appearance: none;
	    cursor:pointer;
	    display:block;
	    width:8px;
	    color: #333;
	    text-align:center;
	    position:relative;
	}

	input[type=number]:hover::-webkit-inner-spin-button { 
	    background: url('../imgs/up-and-down-small-triangular-arrowsWhite.png') no-repeat 0% 0%; 
	    background-size: 100% 100%; 
	    width: 14px;
	    height: 14px;
	    padding: 4px;
	    position: relative;
	    right: 4px;
	    top: 2px;
	}
	select {
	    -webkit-appearance: none;
	    -moz-appearance: none;
	    appearance: none;
	    background-image: url(../imgs/selectArrowWhite.png);
	    background-repeat: no-repeat;
	    background-position: 10px;
	    padding: 5px;
	}
}

.searchResult{
	padding:60px 0;
}
.customSearch{
	padding:30px 15px;
	background: $lightGray2;
	.closeChat{
		display: none;
		cursor: pointer;
	}
	label{
		display: block;
		font-weight: 400;
	}
	h3{
		font-size: 2.5em;
	}
	p{
		color:$medGray;
	}
	.itemDrop {
	    margin-bottom: 15px;
	}
	.orangeBtn{
	    padding: 6px 45px;
	    margin: auto;
	    float: none;
	    margin-top: 30px;
	}
	select{
		width: 100%;
		text-align: right;
		background: transparent;
		border: 1px solid #ebebeb;
		box-shadow: 0 0 0px 1px #ebebeb;
		border-radius: 0;
		color: $medGray;
		height: 34px;
    	padding: 5px;
    	 -webkit-appearance: none;
	    -moz-appearance: none;
	    appearance: none;
	    background-image: url(../imgs/selectArrow.png);
	    background-repeat: no-repeat;
	    background-position: 10px;
	}
	input{
		background: transparent;
	    border: 1px solid #ebebeb;
	    box-shadow: 0 0 0px 1px #ebebeb;
	    border-radius: 0;
	    color: #949b9f;
	    margin:0;
	}
}