/*   fonts     */
@font-face{
	src:url('../fonts/univers-next-arabic-regular.ttf');
	font-family: reg;
}
@font-face{
	src:url('../fonts/SegoeUI.ttf');
	font-family: en;
}
@font-face{
	src:url('../fonts/The-Sans-Plain.otf');
	font-family: plain;
}
@font-face{
	src:url('../fonts/NeoSansArabicRegular.ttf');
	font-family: logoFont;
}
@font-face{
	src:url('../fonts/NeoSansStd-Bold.otf');
	font-family: pold;
}
@font-face{
	src:url('../fonts/JF-Flat-Regular.ttf');
	font-family: flatFont;
}
/*------------vars------------*/
@import '_vars.scss';
/*      shared       */
body{
	font-family: reg;
	color: $gray;
   	height: 100%;
	background:$lightGray;
}
html {
   height: 100%;
}

.content{
	position: relative;
	padding-bottom: 52px;
	min-height: 100%;
}
.blue{
	color: $blue;
}
.blueBtn{
	background:$blue;
	color: #fff;
	border-radius: 0;
	font-size: 1.3em;
}
.blueBtn:hover,.blueBtn:focus{
	color: #fff;
}
a,a:hover,a:focus{
	color: $blue;
	text-decoration: none;
}
h3,.h3{
	font-size: 1.3em;
}
.btn:hover,a:hover{
	opacity: .8;
	transition: all 1s;
}
.login,.register{
	.alert{
		background:$red;
		color: #fff;
		font-family: flatFont;
	    width: 60%;
	    border-radius: 0;
	    margin: auto;
	    box-shadow: none;
	    transition: all 1s;
	}
}
img{
	max-width: 100%;
}
.En{
	font-family: en;
}
.Ar{
	font-family: logoFont;
}
.padd0{
	padding: 0;
}
.orangeBtn{
	background:$orange;
	color: #fff;
	border-radius: 0;
	font-size: 1.2em;
	a{
		color: #fff;
	}
}
.orangeBtn:focus,.orangeBtn:hover{
	color:#fff;
	a{
		color: #fff;
	}
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: $lightGray !important;
}

:-ms-input-placeholder{ /* Internet Explorer 10-11 */
    color: $lightGray !important;
}

::-ms-input-placeholder{ /* Microsoft Edge */
    color: $lightGray !important;
}
.disabled::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: $orange !important;
}
.disabled:-ms-input-placeholder{ /* Internet Explorer 10-11 */
    color: $lightGray !important;
}

.disabled::-ms-input-placeholder{ /* Microsoft Edge */
    color: $lightGray !important;
}
.disabled:focus{
	box-shadow: none;
}
.disabled{
	background: transparent;
    border-bottom: 1px solid #fff !important;
    border-radius: 0 !important;
    cursor: not-allowed;
}
.fa-exclamation{
	background:$blue;
	color: #fff;
	padding: 2px 6px;
    border-radius: 50%;
    margin-left: 5px;
}
.paddR0{
	padding-right: 0;
}
.center{
	text-align: center;
}
header+section{
	padding-top:65px;
}

.whiteBtn{
    border-radius: 0;
    background: #fff;
    font-size: 1.2em;
    color: $orange;
}
.whiteBtn + .orangeBtn{
	margin-right: 15px;
}
.logoImg{
    padding: 4px 3px;
    background: #fff;
    border-radius: 4px;
        text-align: center;
}
#up{
    display: none;
    position: fixed;
    bottom: 100px;
    left: 30px;
    z-index: 99;
    border: none;
    outline: none;
    background-color: #fff;
    border:1px solid $blue;
    color: $blue;
    cursor: pointer;
    border-radius: 0;
    padding: 8px 12px;
}
select {
   -webkit-appearance: none; 
   -moz-appearance: none;
   appearance: none;       /* remove default arrow */
   background-image: url('../imgs/selectArrow.png');
   background-repeat: no-repeat;
    background-position: 10px;   /* add custom arrow */
}
select::-ms-expand { 
    display: none; /* hide the default arrow in ie10 and ie11 */
}
/* alert */
.swal2-popup {
  justify-content: center;
  width: auto !important;
  max-width: 100% !important;
  padding: 8em 16em !important;
}
.swal2-popup .swal2-title{font-size:3em !important}
.swal2-popup .swal2-styled {
  margin: 0 .3125em !important;
  padding: .625em 3.5em !important;
}
.swal2-popup .swal2-styled.swal2-confirm,.swal2-popup .swal2-styled.swal2-cancel{
	font-size:1.3em !important;
}
.notify{
	background: rgba(203, 77, 92, .7);
	color: #fff;
	border: 0;
	display: inline-block;
	position: fixed;
	top: 100px;
    left: 20px;
    padding: 15px;
    display: none;
    transition: opacity 1s;
    .close{
    	color: #fff;
    	opacity: 1;
    	font-weight: 100;
    }
}
section{
	max-width: 100%;
    overflow: hidden;
}
