@media only screen and (max-width: 767px) {
	h3,.h3 {
	    font-size: 1.2em;
	}
	.bar{
		li span{
			font-size:11px;
			padding:15px 5px;
		}
	}
	header+section {
	    padding-top: 120px;
	}
	#specialAD .orangeBtn {
		    padding: 12px;
	}
	/*    login    */
	.login,.register{
		background-size: cover;
	}
	.login .loginCont{
		.submit .btn{
			padding: 6px 18px;
    		font-size: 1.2em;
		}
	}
	.login .loginCont{
		margin:90px 0;
	}
	.login .alert{
	    top: 10px;
	}
	.blueBtn{
		font-size: 1em;
	}
	.logoWrap .Ar {
		font-size: 2em;
	    margin-left: 15px;
	}
	.logoWrap .En{
		font-size: 1em;
	}
	.creditWrap {
	    margin: 0 30px;
	}
	.logoWrap3 .En{
		margin-left: 20px;
	}
	/*      header      */
	header{
		z-index: 2;
		min-height: 120px;
		.container{
			padding-left: 5px;
			padding-right: 5px;
		}
		.bar li a {
		    padding: 15px 5px;
		}
		.bar i {
		    width: 20px;
		    height: 20px;
		    line-height: 20px;
		}
		.bar i.fa-heart, .bar i.fa-user, .bar i.fa-comments,.bar i.fa-plus, .bar i.fa-user-plus{
			font-size: .8em;
		}
		.bar li span {
		    font-size: 10px;
		    padding: 15px 0px;
		}
		.proWrap {
		    width: 30px;
		    height: 30px;
		    margin-bottom: 5px;
		}
	}
	.navbar{
		width: 100%;
		button{
			margin-left: 0;
    		margin-top: 15px;
		}
		.icon-bar{
			background:#fff;
		}
		.navbar-collapse{
			width: 100%;
		}
	}
	.proDet .proName{
		margin-right: 0;
    	position: relative;
    	h4{
    		font-size: 1.8em;
    	}
	}
	.sideMenu{
		padding: 30px 15px;
	}
	.sideMenu .tab-pane h4{
		text-align: center;
	}
	.paddX0{padding: 0;}
	/*    contact   us     */
	.rowFlex .colFlex {
	    flex: none;
	    min-height: 400px;
	}
	.rowFlex {
	    display: block;
	}
	.paddX0{
		padding:0;
	}
	.masioom .centerWrap div:first-child{
		padding-left: 10px;
	}
	.swal2-popup {
    	padding: 2em 4em !important;
    }
    .swal2-popup .swal2-title {
	    font-size: 2em !important;
	}
	.swal2-popup .swal2-styled {
	    padding: .625em 3em !important;
	}

	/*   modal    */
	#registerModal,#loginModal{
		.registerCont{
		    max-width: 90%;
		    margin: 30px auto;
		}
		.loginCont {
		    margin: 90px auto;
		}
		.register{
			.carousel .carousel-indicators {
		    	bottom: 140px;
			}
		}
		.login{
			.carousel .carousel-indicators {
		    	bottom: 140px;
		    }
		}
		.login .alert, .register .alert {
		    top: 0px;
		}
	}
	.theRest {
	    margin-top: 30px;
	}
	.homeBanner{
		.lookFor {
		    padding: 15px;
		}
		.itemDrop {
    		margin-bottom: 15px;
    	}
	}
	/* chat  */
	.openChatPeople{
		display: block;
	}
	.chatPeople{
		display: none;
		margin:15px;
	}
	.chat .chatHead h3 {
	    float: right;
	    margin-top: 30px;
	}
	.closeChat{
		display: block;
		padding-top: 5px;
	}
	.notifyMsg .notifiNum {left: 5px;}
	.chat .tab-content {
	    margin-right: 0;
	    padding: 15px;
	}
	.chat .sendMsg .fileGet {margin-left: 5px;}
	/* subscripe */
	.listOfSubscripe .subscripe {
	    margin: 0 0px 60px;
	}
	/* profile */
	.proDet .imgWrap {
	    max-width: 170px;
	    margin: auto;
	}
	.proDet .proName h4 {
	    font-size: 1.4em;
	}
	.proDet .proName h5 {
	    font-size: 1em;
	}
	.addInput i {
	    left: -20px;
	}
	.logoWrap {
	    margin-bottom: 15px;
	}
	/* map */
	.level1Bubble{
	    top: 150px;
	    right: 20px;
	    padding: 5px;
	    max-width: 150px;
	}
	.level2Bubble {
	    top: 60px;
	    right: 20px;
	    padding: 5px;
	    max-width: 200px;
		.bubbleArrow:after {
	    	bottom: 3px;
	    }
	}
	.bubbleArrow h4 {
	    font-size: 14px;
	}
	.levelUp {
	    top: 60px;
	    left: 10px;
	}
	.blueSearch{
		select{
			margin-bottom: 10px;
		}
	}
	.customSearch {
    	display: none;
    	padding-top: 15px;
    	.closeSearch{
    		padding:10px;
    	}
    	h3{
    		margin-top:0;
    	}
    }
    .openSearch{
    	display: inline-block;
    }
	.prices h3 {
		font-size:1.5em;
	}
	.banner h3 {
	    font-size: 2.5em;
	}
	.banner h4 {
    	font-size: 1.4em;
	}

}
@media only screen and (min-width : 768px) and (max-width : 1024px) {
	.bar{
		margin-top: 0;
	}
	.login,.register{
		background-size: cover;
	}
	h3, .h3 {
	    font-size: 1.5em;
	}
	.login .loginCont {
	    padding: 30px;
		.submit .btn{
			padding: 10px 30px;
    		font-size: 1.2em;
		}
	}
	.logoWrap .Ar{
		font-size: 2.5em;
    	margin-left: 30px;
	}
	.logoWrap .En{
		font-size: 1.2em;
	}
	.login .alert {
	    top: 30px;
	}
	.services {
	   padding: 60px 0px;
	}
	/*     profile     */
	.sideMenu {
	    padding: 60px 15px;
	}
	.proDet .proName {
	    margin-right: 33.33%;
	}
	.nav>li>a {
	    padding: 10px 10px;
	}
	/* add */
	.addCont form .specPadd {
		padding: 60px 10px 60px 0;
	}
	header{
		min-height: 120px;
	}
	header+section {
    	margin-top: 30px;
    	padding-top: 90px;
	}
	.listOfSubscripe .subscripe {
	    margin: 0 0px 60px;
	}
}
@media (min-width: 768px){
	#registerModal,#loginModal,#loginMkatbModal{
		.modal-dialog {
		    width: 800px;
		    max-width: 90%;
		}
	}
}