.detailsCont{
	background:$lightGray;
	.oneHome{
		box-shadow: 0px 4px 10px 1px #ccc;
		margin-top: 30px;
		.since {
		    margin-right: -10px;
		    margin-left: -10px;
		    padding-right: 10px;
		    padding-left: 10px;
		    margin-top: -10px;
		    padding-top: 10px;
		    background: #ebebeb;
		    width: calc(100% + 20px);
		}
	}
}
.icons .popover{
	    background-color: #fff;
	    border:0;
	    box-shadow: none;
	    &.right>.arrow{border-right-color: transparent;}
	    i{
	    	background:transparent;
	    	color: $blue;
	    	box-shadow: none;
	    	padding: 10px 5px;
		    margin-right: 0;
		    width: auto;
		    height: auto;
		    font-size: 1.3em;
		    text-align: center;
		}
}
.popover-content{text-align: center;padding:5px 8px;}
.tooltip.right .tooltip-arrow{
  border-right-color: #fff;
  box-shadow: none;
}
#more{
	float: right;
	transition: all 1s;
}
#detailsMap{
	width: 100%;
	height: 400px;
	.gm-style-iw{
	    top: 15px !important;
	    box-shadow:none;
	    border-radius: 20px;
	    box-shadow: 0 0 1px #949292;
	}
	.gm-style-iw >div {
	    width: 100%;
	    min-height: 100% !important;
	}
	.detailsLocate{
		float: none;
	    width: 100%;
	    display: table-row;
		>div{
			padding:10px;
			height: 100%;
		    display: table-cell;
		    float: none;
		    p{
		    	font-family: reg;
		    	margin-bottom: 0;
		    }
		}
		>div:nth-child(1){
			background:$orange;
			color: #fff;
			text-align: center;
			i{
				font-size: 2em;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
	}
}
.details .container{
	border-bottom: 1px solid $medGray2;
	padding-bottom: 30px;
	padding-right:0;
	padding-left:0;
	> div:first-child{
		padding-right:0;
	}
	> div:last-child{
		padding-left:0;
	}
}
#detailsCarou{
	.item{
		width: 100%;
		height: 400px;
		overflow:hidden;
		img{
			min-width: 100%;
		    min-height: 100%;
		    max-width: 200%;
		}
	}
	.carousel-indicators li {
	    width: 12px;
	    height: 12px;
	    border-radius: 0;
		margin: 0 3px;
		border:0;
		background: rgba(255,255,255,.3);
	}
	.carousel-indicators li.active{
		background:#fff;
	}
	.carousel-control i{
		background:$blue;
		padding:5px 10px;
		color: #fff;
	}
}
.detailsCont{
	.nav-pills{
		text-align: center;
		box-shadow: 0px 1px 5px 0px #b2b9bd;
    	position: relative;
    	background:$lightGray2;
	}
	.nav-pills>li {
	    float: none;
	    display: inline-block;
	    a{
	    	border-radius: 0;
    		padding: 20px 60px;
    		color: $medGray;
    		i{
    			margin-left: 10px;
    		}
	    }
	    &.active>a,&.active>a:focus, &.active>a:hover {
		    color:$gray;
		    background-color: $lightGray;
		    i{
		    	color: $blue;
		    }
		}
	}
}
.details{
	padding: 60px 0 30px;
}
.inline{
	float: right;
	width: 100%;
	clear: both;
	margin: 10px 0;
	>h5{
		float: right;
		color: $medGray;
		margin-left: 10px;
		i{margin-left:5px;}
	}
}
.flatDetails{
	h3{
		font-family: logoFont;
		margin-bottom:15px;
	}
	p{
		line-height: 2em;
	}
	a{
		i{
			margin-right:5px;
		}
	}
	label{
		margin-bottom:15px;
		font-weight: 500;
		span{margin-right: 5px;}
	}
}
.det {
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid $medGray2;
    display: block;
    float: right;
    width: 100%;
    label{
    	font-weight: 400;
    }
}
.desc{
	float: right;
	width: 100%;
	.itemDesc{
		background:$lightGray2;
		margin-bottom:5px;
		padding:10px 15px;
		height: 52px;
		overflow:hidden;
	}
	img{
	    float: right;
	    margin-left: 10px;
	    max-width: 32px;
	}
	i{
		float: right;
		color: $medGray;
		margin-top: 10px;
		margin-left: 10px;
	}
	p{
		float: right;
		margin-bottom:0;
	}
}
.icons i {
    background: #fff;
    padding: 10px;
    margin-right: 10px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    box-shadow: 0px 1px 1px 0px #b2b9bd;
    cursor: pointer;
    &.fa-heart{
    	color: $blue;
    }
    &.fa-share-alt{
    	color: $orange;
    }
}
.check {
    display: block;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    float: right;
    margin-left: 10px;
    margin-right: 10px;
    input {
	    position: absolute;
	    opacity: 0;
	    cursor: pointer;
	}
	.checkmark {
	    position: absolute;
	    top: 0;
	    left: 0;
	    height: 25px;
	    width: 25px;
	    background-color: #fff;
	}
	input:checked ~ .checkmark {
	    background-color: $orange;
	}
	.checkmark:after {
	    content: "";
	    position: absolute;
	    display: none;
	}
	input:checked ~ .checkmark:after {
	    display: block;
	}
	.checkmark:after {
	    left: 8px;
    	top: 4px;
	    width: 8px;
	    height: 14px;
	    border: solid white;
	    border-width: 0 3px 3px 0;
	    -webkit-transform: rotate(45deg);
	    -ms-transform: rotate(45deg);
	    transform: rotate(45deg);
	}
}
.owner{
	float: right;
    width: 100%;
    background: #fff;
    box-shadow: 0 0 6px 1px #d2d2d2;
    padding: 30px 0;
    h3{
    	margin-top: 10px;
    }
    p{
    	color: $medGray;
    }
    .circle-img{
    	margin-bottom: 15px;
    }
    .textBubble{
    	position: relative;
    	margin-top: 45px;
    }
    textarea{
    	background:$lightGray;
    	border:0;
    	box-shadow: none;
    	min-height:200px;
    	resize: vertical;
    }
    textarea:focus{
    	box-shadow: none;
    }
    .textBubble:before{
    	content: "";
	    position: absolute;
	    top: -28px;
	    right: -10%;
	    height: 30px;
	    border-right: 60px solid #ebebeb;
	    background: #ebebeb;
	    -webkit-border-bottom-right-radius: 80px 50px;
	    -moz-border-radius-bottomright: 80px 50px;
	    border-bottom-right-radius: 100px 50px;
	    -webkit-transform: translate(0, -2px);
	    -moz-transform: translate(0, -2px);
	    -ms-transform: translate(0, -2px);
	    -o-transform: translate(0, -2px);
	    transform: translate(-30px, -2px) rotate(180deg);
	    z-index: 2;
    }
    .textBubble:after{
    	content: "";
	    position: absolute;
	    top: -28px;
	    right: -10%;
	    width: 41px;
	    height: 30px;
	    background: #fff;
	    -webkit-border-bottom-right-radius: 40px 50px;
	    -moz-border-radius-bottomright: 40px 50px;
	    border-bottom-right-radius: 40px 50px;
	    -webkit-transform: translate(-30px, -2px);
	    -moz-transform: translate(-30px, -2px);
	    -ms-transform: translate(-30px, -2px);
	    -o-transform: translate(-30px, -2px);
	    transform: translate(-30px, -2px) rotate(180deg);
	    z-index: 2;
	}


    .submit .btn {
	    padding: 6px 48px;
	}
}
.owner-det{
	float: right;
	width: 100%;
	i{
		margin-top: 3px;
    	font-size: 1.2em;
	}
}
a{
	cursor: pointer;
}