.wrapSer{
	text-align: center;
	max-height: 160px;
	overflow:hidden;
	.blue{
		background:$blue;
	}
	.icon1,.icon2{
		width: 160px;
		height: 160px;
     	margin: auto;
		border-radius: 50%;
		padding:30px;
		text-align: center;
     }
	img{
		max-width: 100px;
		max-height: 100px;
	}
	.icon2{
		display:none;
	}
}
.services{
	padding:60px;
	background: url('../imgs/serBack.png') no-repeat;
	background-size: cover;
	color: #fff;
}
.service{
	text-align: center;
	cursor: pointer;
	margin:30px 0;
	h3, .h3 {
	    font-size: 2em;
	}
	h4{
		padding:15px;
	}
	&:hover{
		.moreLink{
			opacity: 1;
		}
	}
}
.moreLink{
	opacity: 0;
	transition: all 1s;
}