header{
	background:rgba(0,36,57,.9);
	float: left;
	width:100%; 
	position: absolute;
    top: 0;
    z-index: 2;
    right: 0;
    min-height: 65px;
}
.nav>li>a {
   padding: 10px 10px;
}
.loginMenu  li a{
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	max-width: 90px;
}
.bar{
	float: left;
	margin-top: 5px;
	margin-bottom: 0;
	li{
		display: inline-block;
		float: right;
		a{
			color: #fff;
		    padding: 15px 10px;
		    display: inline-block;
			
		}
	}
	i{
		color: #fff;
		width: 24px;
		height: 24px;
		text-align: center;
		border-radius: 50%;
		text-align: center;
		line-height: 24px;
	}
	i.fa-heart,i.fa-user,i.fa-comments{
		background:$blue;
    	font-size: 1em;
	}
	i.fa-plus,i.fa-user-plus,i.fa-building{
		background:$orange;
    	font-size: 1em;
	}
}
.notifi{
	position: relative;
	display: inline-block;
	.notifiDot{
		position: absolute;
		background:$orange;
		width: 7px;
	    height: 7px;
	    border-radius: 50%;
	    top: 0px;
	    right: 0px;
	}
}
.wrapIcon {
    float: right;
    margin-top: 15px;
}
.proWrap{
	width: 40px;
	height: 40px;
	overflow: hidden;
	border-radius: 50%;
	float: right;
		margin-top: 6px;
	img{
		min-height: 100%;
	}
}
.navbar{
	border-radius: 0;
	float: right;
	margin-bottom: 0;
	.navbar-collapse{
		float: left;
	}
	.nav>li>a:focus, .nav>li>a:hover ,.nav>li.activeLi>a{
	    text-decoration: none;
	    background-color: transparent;
	    border-bottom: 1px solid $blue;
	    transition: all .5s;
	    opacity: 1;
	}
}
.navbar-brand{
	max-width: 160px;
	height: auto;
}
.logoWrap2{
	.logoImg{
		padding: 4px 3px;
		background:#fff;
		border-radius: 4px;
	}
	.En{
		font-size: .7em;
	    line-height: .5em;
	    float: left;
	    color: #fff;
	}
	.Ar{
		font-size: 1.3em;
    	color: #fff;
	}
}
.navbar-nav>li>a {
    padding-top: 20px;
    padding-bottom: 20px;
    color: #fff;
}
/*      banner       */
.banner{
	background-color: $darkBlue;
	background-size: cover;
	padding: 140px 0;
	color: #fff;
	text-align: center; 
	background-repeat: no-repeat;
	h3{
		font-size: 3em;
		margin-bottom: 30px;
		font-family: logoFont;
	}
	h4{
		font-size: 1.5em;
	}
}
.proBanner{
	background-image:url('../imgs/headBack.jpg');
}