.subscripeBanner{
	background-image: url('../imgs/subsBanner.png')
}
#mySubscripe{
	font-family: logoFont;
	h4 .orange{
		margin: 0 5px;
	}
}
.listOfSubscripe{
	background:$lightGray;
	padding:60px 0;
	.subscripe{
		margin:0 60px 60px;
	}
}
.price:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 15px solid transparent;
	border-top-color: #fff;
	border-bottom: 0;
	margin-left: -15px;
    margin-bottom: -15px;
    z-index: 2;
}
.subscripe{
	border-radius: 10px;
	box-shadow: 1px 1px 6px 0px #c7c7c7;
	.price{
		position: relative;
		padding:15px;
		background:url('../imgs/bankBg.png');
		color: $medGray;
		border-top-left-radius: 10px;
		border-top-right-radius: 10px;
		.number{
			color: $blue;
			font-size:4.5em;
		}
		h5{
			display: inline-block;
			font-size: 1.4em;
			color: $gray;
		}
	}
	.duration{
		background:$lightGray2;
		padding-bottom: 30px;
		position: relative;
		border-bottom-left-radius: 10px;
		border-bottom-right-radius: 10px;
		color:$medGray;
		.pold{
			color: $gray;
		}
		p{
			text-align: center;
			padding:30px 0;
			margin-bottom: 0;
		}
		.pItems p:not(:last-child){
			border-bottom: 1px solid #fff;
		}
		a{
			margin:0;
			position: absolute;
			bottom:-20px;
			left: 50%;
			-webkit-transform: translateX(-50%);
			transform: translateX(-50%);
			padding: 12px 40px;
    		border-radius: 4px;
		}
	}
}
.theRest{
	margin-top: 160px;
	p{
		text-align: right;
		padding:30px 0;
		margin-bottom: 0;
		color: $medGray;
		span{color: $gray;margin:0 5px;}
	}
}
.pold{
	font-family: pold;
}