.greyBack{
	background:$lightGray;
}
.prizeBanner{
	background-image: url('../imgs/brizeBanner.png');
	background-repeat: no-repeat;
}
.prices{
	text-align: center;
    padding: 60px 0;
    h3{
    	font-size: 2em;
    	margin-bottom: 30px;
    }
    h5{
    	margin-bottom: 30px;
    	color: $medGray;
    }
}
.blueSearch .prizeBtn{
	margin-top:3px;
	border-width: 1px;
	height: 34px;
	border-color: rgba(255,255,255,0.5);
}
.striped{
	max-height:300px;
	overflow-y: scroll;
	> div{
		padding:15px;
	}
	& > div:nth-child(even){
		background:transparent;
	}
	& > div:nth-child(odd){
		background:$lightGray2;
	}
	p span {
		margin-right: 15px;
	}
}
.chart{
	padding:60px 0;
}