.chat{
	background:$lightGray;
	>div {
		padding: 60px 0;
	}
	.chatPeople{
		background:#fff;
		border-radius: 4px;
		padding: 0 15px;
		.nav-tabs{
			border:0;
			display: block;
			clear: both;
			margin: 0 -15px;
		}
	}
	.chatHead{
		float: right;
		width: 100%;
		border-bottom: 1px solid $lightGray;
		.left{
			float: left;
		}
		h3{
			float: right;
		}
	}
	.chatMsg{
		width: 100%;
    	float: right;
    	h4{
    		color: $gray
    	}
	}
	.circle-img{
		width: 45px;
		height: 45px;
		overflow:hidden;
		float: right;
		img{
			min-width: 100%;
			min-height:100%;
		}
	}
	.duration{
		color: $medGray;
		span{
			float: left;
		}
	}
	.outMsg{
		color: $medGray;
	}
	.nav-tabs>li {
		width: 100%;
		a{
			float: right;
    		width: 100%;
    		padding-right:0;
    		padding-left: 0;
		}
	}
	.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover {
		border:0;
		background: $lightGray2;
	}
	.openChat {
		background:$lightGray2;
		float: right;
		width: 100%;
		padding:15px;
	}
	.openChatHead{
		float: right;
	    width: 100%;
	    border-bottom: 1px solid $lightGray;
	    padding-bottom: 15px;
	    button{
	    	float: left;
	    	margin-top:10px;
	    	i{
	    		color: $medGray;
	    		font-size: 1.4em;
	    	}
	    }
	    h3{
	    	margin-top: 10px;
	    }
	}
	.openChatBody{
		float: right;
	    width: 100%;
	    margin: 15px 0;
	    max-height: 250px;
	    .no-btn{
	    	max-width: 10%;
	    }
	}
	.tab-content{
		margin-right: 10px;
	}
	.sendMsg{
		clear: both;
		background:#fff;
		float: right;
		width: 100%;
		padding:15px;
		.chatSendMsg{
			width: 80%;
    		float: right;
    		margin-bottom: 0;
		}
		.form-control{
			border:0;
			box-shadow: none;
			width: 80%;
    		float: right;
    		margin:0;
    		padding:0;
		}
		.fileGet{
			position: relative;
			cursor: pointer;
			text-align: center;
			margin-top: 10px;
			margin-left: 10px;
			float: left;
			i{
				position: absolute;
				z-index: 0;
				transform: rotate(-45deg);
				color: $lightGray;
				font-size:1.4em;
			}
			label{
				z-index: 2;
			}
		}
		.sendMsgBtn{
			background:$blue;
			color: #fff;
			border-radius: 50%;
			padding:10px;
			float: left;
			i{
				transform: rotate(-120deg);
			}
		}
	}
}
.del{
	color: $medGray;
	margin-top: 20px;
}
.no-btn{
	background:transparent;
	border:0;
	padding:0;
}
.openChatPeople{
	display: none;
	background: $blue;
    padding: 10px;
    color: #fff;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin: 15px;
    font-size: 1.4em;
    cursor: pointer;
}
.no-msg{
	text-align: center;
    padding: 60px;
    color: $gray2;
    background:$lightGray2;
    i{
    	font-size: 12em;
    }
    p{
    	font-size:3em;
    }
}
.closeChat{
	display: none;
	padding: 0;
	float: left;
	cursor: pointer;
	i{
		font-size: 1.4em;
		color: $blue;
	}
}
.chatBubble {
	position: relative;
	border-radius:20px;
	padding: 15px;
	margin-bottom: 10px;
	max-width:85%;
	.circle-img{
		margin-top:5px;
	}
}

.chatBubble:after {
	content: '';
	position: absolute;
	top: 50%;
	width: 0;
	height: 0;
	margin-top: -10px;
	border: 10px solid transparent;
}
.recieved{
	width: 100%;
    float: right;
	.chatBubble{
		background:#fff;
		float: left;
		&:after{
			left: 0;
			border-right-color:#fff;
			border-left: 0;
			margin-left: -10px;
		}
	}
	.no-btn{
		float: left;
		margin-left:15px;
	}
	.circle-img{
		float: left;
	}
	.time{
    	float: left;
	}
}
.time{
	display: block;
    clear: both;
	p{
		color: $medGray;
	}
}
.sent{
	width: 100%;
    float: right;
	.chatBubble{
		float: right;
		background:$blue;
		color: #fff;
		&:after{
			right: 0;
			border-left-color: $blue;
			border-right: 0;
			margin-right: -10px;
		}
	}
	.no-btn{
		margin-right:15px;
	}
	.time{
    	float: right;
	}
}
.notifyMsg{
	position: relative;
	.notifiNum{
		position: absolute;
	    background: #f5881e;
	    color: #fff;
	    padding: 0px 6px;
	    width: 20px;
	    height: 20px;
	    border-radius: 50%;
	    left: 15px;
	}
}
.circle-img{
	border-radius: 50%;
}
.iconBtn{
	background:transparent;
	border:0;
	padding:0;
}