.grayForm{
	background:$lightGray;
	margin-bottom: 0;
	label{
		margin-bottom: 0px;
		font-weight: 400;
		span{
			margin-right: 5px;
		}
	}
	select{
		width: 100%;
	    border: 0;
	    margin: 15px 0;
	    border-radius: 4px;
	    height: 34px;
	    padding:5px;
	    background-color:$lightGray2;
	}
	label + span{
		margin-right: 5px;
	}
	h3{
		margin-top: 0;
		margin-bottom: 30px;
	}
	textarea{
		margin-top: 15px;
	}
	.form-control {
		background:$lightGray2;
	}
	.dropdown{
		margin-top: 15px;
		margin-bottom: 15px;
		button{
		background:$lightGray2;
		text-align: right;
		width: 100%;
			span{
				float: left;
				color: $blue;
				margin-top: 8px;
			}
		}
	}
	.wBorder:last-child{
		border-bottom: 0;
	}
	.submit .btn {
	    margin-top: 30px;
	    padding: 16px 70px;
	}
	input[type="checkbox"] {
	    margin-bottom: 15px;
	}
}
.orange{
	color: $orange;
}
.border{
	float: right;
    border: 1px solid $gray2;
    padding: 3px 5px;
    margin-left: 15px;
}
.addCont form{
	padding: 60px 0; 
	max-width: 100%;
    overflow: hidden;
	.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover {
	    color: #555;
	    cursor: default;
	    background-color: $lightGray2;
	    border-bottom: 0;

	}
	.nav-tabs {
	    border-bottom: 0;
	}
	.nav-tabs>li>a{
		color: $gray;
	}
	.tab-content{
		background:$lightGray2;
		float: right;
		width: 100%;
	}
	.specPadd{
		padding: 60px 10px;
		img{
			float: left;
		}
		h5{
			display: block;
		}
	}
	#video{
		text-align: center;
    	padding: 30px 15px;
	}
	.tab-content>.active {
	    float: right;
	    width: 100%;
	}
}
.wBorder {
    padding: 30px 0;
    border-bottom: 1px solid #fff;
}
.fileUpload{
	position: relative;
	width: 35px;
	height: 35px;
	float: none;
	margin: 50px auto;
	button{
		padding: 0;
	    width: 100%;
	    height: 100%;
	    text-align: center;
	    background: $lightGray2;
	    display: block;
	    color: $orange;
	    border-color: $orange;
	    border-radius: 50%;
		i{
			color: $orange;
			text-align: center;
			line-height: 35px;
		}
	}
	input{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		cursor: pointer;
	}
}
.disableInput{
	cursor: not-allowed;
	opacity: .6;
	border-bottom: 0;
	select{
		pointer-events: none;
	}
}
.uploadV{
	display: none;
}
#map{
	width: 100%;
	height: 300px;
}
#searchMap{
	border: 0;
    box-shadow: 0px 0px 1px 0px #888888;
    padding: 8px 12px;
    margin-top: 10px;
}
.alert-success{
	background: $blue;
    border: 0;
    color: #fff;
    border-radius: 0;
    .close{
    	color: #fff;
	    opacity: 1;
	    line-height: .5;
    }
}
.container .previewChosen{
	margin-bottom: 30px;
}
.pagination>.disabled>a, .pagination>.disabled>a:focus, .pagination>.disabled>a:hover, .pagination>.disabled>span, .pagination>.disabled>span:focus, .pagination>.disabled>span:hover{
	background:transparent;
}
#completeProfile{
	img{
		margin-bottom:30px;
	}
	.orangeBtn{
		margin-top: 30px;
	}
	.cancel{
		color: $gray;
		display: block;
		clear: both;
		margin-top:0;
	}
}