#imagemodal{
	text-align: center;
	.modal-header{
		padding: 5px;
		.close{
			color: $gray;
		}
	}
	.modal-dialog {
	    width: auto;
	    margin: 30px 60px;
	    display: inline-block;
	}
}
.modal{
    overflow-y: scroll;
}
.modal-header{
	    position: absolute;
	    top: 0;
	    left: 0;
	    z-index: 2;
	    border: 0;
	    .close{
	    	color: #fff;
	    	opacity:1;
	    }
}
#registerModal,#loginModal,#loginMkatbModal{
	.modal-header{
		padding: 10px 20px;
	}
	.registerCont{padding:0;}
	.registerCont,.loginCont{
		max-width:80%;
	}
	.modal-body {
	    padding:0px;
	    text-align:right;
	}
	.login .alert, .register .alert {
	    top: 20px;
    }
    .close {
    	color: $gray;
    }
}

.welcome{
    text-align: center;
    display:none;
    background:url('../imgs/welcomeBack.png') no-repeat;
    background-position: 50% -50%;
    padding-bottom: 30px;
    h3{
    	line-height: 2em;
    }
    button{
    	padding: 10px 50px;
    }
    a.notNow{
    	margin: 15px 0px;
    }
}
a.notNow{
	color:$gray;
	display:block;
	clear:both;
}
#specialAD{
	.modal-body{padding: 30px 15px;}
	img{
		margin-bottom: 15px;
	}
	h3{
		font-size: 2em;
    	margin-bottom: 30px;
	}
	.orangeBtn{
		padding: 12px 50px;
	    border-radius: 4px;
	    margin-top: 30px;
	}
	.cancel{
	    margin-top: 0px;
	    display: block;
	    color: $gray;
	}
	.modal-header .close {
    	color: $gray;
    }
}