.contactBanner{
	background-image:url('../imgs/contact.jpg');
}
.contactDet{
	background:#fff;
	margin-top:-30px;
	margin-bottom:-30px;
	position: relative;
	z-index: 2;
	.masioom{
		background:url('../imgs/bannerHome.jpg');
		position: relative;
	}
	.logoImg{
		text-align: center;
	    background: #fff;
	    border-radius: 4px;
	    padding: 5px;
	}
	.En{
		display: block;
	    line-height: 0.1;
	    float: left;
	    font-size: 1.3em;
	    color: #fff;
	}
	.Ar{
		font-size: 2.7em;
    	line-height: .9em;
    	color: #fff;
	}
}
.info{
	text-align: center;
	padding: 30px;
	i{
		color: $blue;
		font-size: 3em;
	}
	h5{
		color: $medGray;
	}
}
.rowFlex{
	display: flex;
	.colFlex {
	  flex: 1;
	}
}
.centerWrap{
	width: 100%;
	float: right;
	position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.blueForm{
	background:$blue;
	color: #fff;
	padding:30px;
	.form-control{
		background: transparent;
	    border-top: 0;
	    border-right: 0;
	    border-left: 0;
	    border-color: rgba(255,255,255,.5);
	    padding-right: 0;
	    box-shadow: none;
    	border-radius: 0;
    	color: #fff;
	}
	button{
		border:2px solid #fff;
		background:transparent;
		color: #fff;
		border-radius: 0;
		padding: 8px 40px;
	    font-weight: 500;
	    margin-top: 30px;
	}
}
.contactMsg .colFlex input.form-control {
    width: 100%;
}
.blueForm button:hover{
	background: rgba(255,255,255,.2);
    color: #fff;
}
.blueAlert{
	padding: 10px;
    background: #fff;
    color: $blue;
    border-radius: 0;
    .close{
	    line-height: .6;
	    opacity: 1;
	    color: $blue;
	}
}
#mapContact{
	height: 700px;
}