
/*   map    */
#map2.fullMap{
	height: 100%;
	width: 100%;
    .gm-style-iw {
       top: 0 !important;
       box-shadow: 0 1px 6px rgba(178, 178, 178, 0.6);
       border-radius: 2px 2px 0 0;
    }
}
.labels{
	color: #fff;
	font-family: reg;
	font-size: 1em;
	opacity: 1;
    pointer-events: none;
    text-align: center;
    width: 60px;
    white-space: nowrap;
    margin-top: -50px !important;
}
.oneHome{
	font-family: reg;
}
.gm-style-iw {
   width: 450px !important;
   left: 0 !important;
   background-color: #fff;
   .flatDetails{
        float: right;
        width: 100%;
        padding: 0 10px;
   }
}
.levelUp{
	position: absolute;
    top: 120px;
    left: 60px;
    width: 30px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    font-size: 1.5em;
    background: #f5881e;
    color: #fff;
    box-shadow: 0px 1px 3px 0px $medGray;
    cursor: pointer;
    display: none;
    i{position: relative;}
}
.level1Bubble,.level2Bubble{
	position: absolute;
	top: 120px;
	right: 60px;
	background:#fff;
	text-align: center;
    padding: 10px;
    max-width: 220px;
    select{
    	border: 0;
    	min-width: 90px;
    }
    .selectWrap{
    	border:2px solid $lightGray;
    	padding: 0 5px;
    }
}
.level2Bubble{display: none;}
.bubbleArrow{
	position: relative;
}
.bubbleArrow:after{
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0;
	height: 0;
	border: 28px solid transparent;
	border-top-color: #fff;
	border-bottom: 0;
	border-left: 0;
	margin-left: -60px;
	margin-bottom: -35px;
}
.gm-style-iw .details2{
    float: right;
    width: 100%;
    padding:0;
    font-family: reg;
    >div{
        padding:0;
    }
    #detailsCarou .item {
        height: 250px;
    }
    .left.carousel-control {
        i{right: 15px;}
    }
    .right.carousel-control {
        i{left: 15px;}
    }
    .owner{
        box-shadow: none;
    }
}